.hero-section{
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    height: 400px;
    padding:0;
    margin:0;
    display:flex;
    align-items: center;
    position: relative;
    *{
        color:#fff;
    }
    &__col{
        display: flex;
        align-items: center;
        z-index:1;
        width:100%;
        @media (min-width: $breakpoint-md) {
            flex:1;
            width:auto;
        }
    }
    &--image{
        &:before{
            content:"";
            position:absolute;
            left:0;
            top:0;
            height:100%;
            width:100%;
            background-color:rgba(0,0,0,0.6);
        }
    }
    &__content{
        h1, h2, h3, h4, h5, h6{
            margin:0;
            padding:0;
        }
        p{
            color:#ffb739;
            text-transform:uppercase;
            font-weight:700;
        }
    }
    &__login{
        margin-left:auto;
        padding:25px;
        background:rgba(42,46,55,0.8);
        margin-right: auto;
        form{
            p{
               label, input{
                   &:not([type="checkbox"]){
                    width:90%;
                   }
               } 
               input{
                    background:rgba(255,255,255,0.5);
                    box-shadow:none;
                    border:none;
                    padding:15px;
                    margin-top:10px;
                }
            }
            .button{
                background-color:#2a7ab0;
                width:97%!important;
            }
        }
    }
}