footer {
    background-color: $darkgrey;
    height: 60px;
    padding:10px 0;

    p {
       //padding: 10px 0; 
        color: $lightblue;
        margin:0;
        text-transform: uppercase;
        letter-spacing:1px;
        position:relative;
        line-height:2;
        &:after{
            content:"";
            height: 2px;
            width: 90%;
            position: absolute;
            bottom: 42%;
            left: 0;
            background: #fff;
        }
        a{
            display:block;
            color:$grey;
            text-decoration: none;
            text-transform: none;
            font-size:12px;
        }
    }
}