.button{
    display: inline-block;
    color: #2a2e37;
    font-size: .875rem;
    line-height: 1.2;
    background-color: transparent;
    border: 3px solid transparent;
    border-radius: 3px;
    padding: 8px 15px;
    font-weight: 700;
    text-transform: uppercase;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
    position: relative;
    width: 100%;
    -webkit-transition: .3s all ease;
    transition: .3s ease all;
}