body {
    margin: 0;
    font-family: "SuisseIntl","Helvetica Neue",Arial,sans-serif;
    background-color: $darkgrey;
    position: relative;
}

html {
    scroll-behavior: smooth;
    overflow-x:hidden;
}

main{
    min-height:calc(100vh - 192px);
    //background:$darkgrey;
    display: flex;
    align-items: center;
}

.container-lg {
    max-width: 1900px;
    margin: 0 auto;
    padding: 0px 15px;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0px 15px;
    display: flex;
    width: 100%;
    flex-wrap:wrap;
    @media (min-width: $breakpoint-md) {
        flex-wrap:nowrap;
    }

    &--sm{
        max-width: 580px;
    }

    &--md{
        max-width: 730px;
    }

    &--lg{
        max-width: 1900px;
    }
}

.container-md {
    max-width: 730px;
    margin: 0 auto;
    padding: 0px 15px;
}

.container-sml {
    max-width: 580px;
    margin: 0 auto;
    padding: 0px 15px;
}

.flex {
    display: flex;
    justify-content: space-between;
    position: relative;
    width:100%;
    &.centered{
        align-items: center;
    }
}

.banner {
  background-image: url(../../images/bg4-01.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  height: 400px;
  *{
    color:#fff;
}
}

.banner-two {
  background-image: url(../../images/bg5-01.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  height: 400px;
  *{
    color:#fff;
}
}

.heading-container {
    padding-top: 50px;

    .heading-wrapper {
        //background-color: $orange;
        //max-width: 550px;
        position: relative;

        &:before {
          content:'';
          position: absolute;
          top: 0;
          bottom: 0;
          right: 100%;
          left: -500px;
          background-color: $orange;
        }
    }
}

.reg-cta, .button {
  //@extend %reg;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .flex {
        flex-wrap: wrap;
    }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 601px) 
and (max-width: 768px) {
    .flex {
        flex-wrap: wrap;
    }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px)
and (max-width: 991px) {
  .flex {
        flex-wrap: wrap;
    }
}

